html {
  height: 100%;
  background-repeat: no-repeat;
  background-size: cover;
  justify-content: center;
  align-items: center;
  display: flex;
}

body {
  height: 100%;
  width: 100%;
  background-color: #fff;
  margin: 0;
}

.main {
  height: 100%;
}

.chat-main {
  height: 100%;
  grid-template-rows: 1fr;
  grid-template-columns: 320px 1fr;
  display: grid;
  overflow: hidden;
}

.chat-main .chat-list {
  height: 100%;
  background: #fbfbfb;
  border: 1px #eaeaea;
  border-right-style: solid;
  padding: 0 5px;
  display: block;
}

.chat-search {
  width: 290px;
  height: 32px;
  text-align: center;
  background: #efefef;
  border-style: solid;
  border-color: #efefef;
  border-radius: 5px;
}

.form-group {
  text-align: center;
}

.link-button {
  text-align: right;
  color: #999;
  margin: 10px;
  font-family: Inter;
  font-size: 15px;
  font-style: normal;
  font-weight: 500;
  line-height: 12px;
}

.chat-list-ui {
  display: block;
}

.chat-item {
  cursor: pointer;
  border-bottom: 1px solid #c5c1c1;
  border-radius: 0;
  margin-left: -36px;
  padding: 2px 0;
  display: block;
  position: relative;
}

.chat-item:hover {
  background-color: #c5c1c1;
  border-radius: 5px;
}

.chat-item-info {
  grid-template-columns: 15% 1fr 10%;
  display: grid;
}

.chat-item-name {
  color: #1e1e1e;
  text-align: left;
  margin-left: 20px;
  font-family: Inter;
  font-size: 13px;
  font-style: normal;
  font-weight: 600;
  line-height: 10px;
  display: block;
}

.chat-name-header {
  margin-top: 15px;
}

.chat-item-img {
  margin: auto 0;
  bottom: 0;
}

.chat-img {
  width: 47px;
  height: 47px;
  text-align: left;
  border-radius: 100px;
  display: block;
  left: 10px;
}

.chat-about {
  margin: auto 0;
  padding: 0;
  display: block;
  bottom: 0;
}

.chat-item-about2 {
  margin: auto 0;
  display: block;
  bottom: 0;
}

.chat-item-about-rows {
  height: 100%;
  grid-template-rows: 1fr 1fr;
  display: grid;
}

.chat-item-time {
  text-align: right;
  color: #999;
  font-family: Inter;
  font-size: 9px;
  font-style: normal;
  font-weight: 500;
  line-height: 13px;
}

.chat-item-unread {
  width: 20px;
  height: 20px;
  text-align: right;
  background-color: #3369f3;
  border-radius: 50%;
  margin-left: 10px;
}

.chat-item-unread-text {
  text-align: center;
  color: #fff;
  height: 100%;
  margin: 2px;
  font-family: Inter;
  font-size: 11px;
  font-style: normal;
  font-weight: 500;
  line-height: 12px;
}

.chat-item-message {
  color: #999;
  text-align: left;
  margin-left: 20px;
  padding: 5px 0;
  font-family: Inter;
  font-size: 12px;
  font-style: normal;
  font-weight: 500;
  line-height: 15px;
}

.chat-main .chat-container {
  padding: 5px;
  display: block;
}

.chat-container-rows {
  height: 100%;
  grid-template-rows: 50px auto 50px;
  grid-template-columns: 1fr;
  display: grid;
}

.chat-container-profile-head {
  border-bottom: 1px solid #c5c1c1;
  display: block;
}

.chat-container-profile-head-column {
  height: 100%;
  width: 100%;
  grid-template-columns: 50px auto 3%;
  display: grid;
}

.chat-item-button-info {
  height: 45px;
  background-color: #fff;
  border: none;
  display: block;
}

.chat-container-dialog {
  height: 100%;
  grid-template-rows: 10% auto;
  display: grid;
}

.chat-container-footer {
  width: 100%;
  text-align: center;
  border-top: 1px solid #c5c1c1;
  padding: 5px;
  display: block;
}

.chat-container-footer-input {
  text-align: center;
  width: 90%;
  height: 32px;
  background: #efefef;
  border: 0 solid;
  border-radius: 30px;
}

.chat-container-footer-input:focus {
  outline-width: 0;
}

.chat-container-button {
  color: #fff;
  vertical-align: top;
  background-color: #fff;
  border: #fff;
  margin: 4px 0 0;
  padding: 0;
  display: inline-block;
}

.chat-container-button-info-icon {
  width: 10px;
  height: 10px;
}

.chat-container-button-send-icon {
  width: 25px;
  height: 25px;
}

.dialog-list {
  height: 100%;
  line-height: auto;
  margin: 0 10px;
  list-style: none;
}

.message-li {
  margin-top: 15px;
  line-height: 1;
}

.message {
  height: auto;
  border: 0 solid;
  border-radius: 5px;
  display: grid;
}

.message .message-request {
  color: #1e1e1e;
  text-align: left;
  max-width: 350px;
  min-height: 50px;
  background-color: #f8f8f8;
  border-radius: 5px;
  grid-template-rows: 1fr 20%;
  margin: 5px;
  padding: 7px;
  display: grid;
}

.message .message-response {
  color: #1e1e1e;
  text-align: right;
  max-width: 350px;
  min-height: 50px;
  background-color: #e4edfd;
  border-radius: 5px;
  grid-template-rows: 1fr 20%;
  margin: 5px 0 5px auto;
  padding: 7px;
  display: grid;
}

.message-content {
  color: #1e1e1e;
  display: block;
}

.message-time {
  color: #3369f3;
  font-size: 9px;
  line-height: 9px;
  display: block;
}

.dialog-last-date {
  text-align: center;
  color: #999;
  padding: 10px;
  font-family: Inter;
  font-size: 12px;
  font-style: normal;
  font-weight: 500;
  line-height: 12px;
}

